<template>
    <div class="max-w-min relative" @click="toggleMenu">
        <span class="text-xl  flex items-center justify-center z-50">
            <button
                :class="{ 'is-active': show }"
                class="hamburger hamburger--elastic "
                type="button"
                ref="hamburger"
            >
                <span class="hamburger-box" id="clicker">
                    <span class="hamburger-inner "></span>
                </span>
            </button>
        </span>

        <!-- menu -->
        <transition name="slide-fade">
            <div
                v-show="show"
                id="menu"
                class="absolute left-0  rounded-lg shadow-lg  "
                :class="bg"
                :style="{ top: top }"
            >
                <div>
                    <ul :style="{ color: text }" class="py-2">
                        <li v-for="(item, i) in items" :key="i" class="px-4 py-3 " id="item">
                            <a href="" class="capitalize font-semibold ">{{
                                item.text
                            }}</a>
                        </li>
                        <li
                            v-show="!items"
                            v-for="(link, i) in links"
                            :key="i"
                            class="  px-4 py-3 "
                        >
                            <a href="" class="hover:text-gray-400 capitalize font-semibold">{{
                                link
                            }}</a>
                        </li>
                    </ul>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
export default {
    props: {
        items: {
            type: Array,
        },
        bg: {
            type: String,
            default: "bg-gray-100",
        },
        text: {
            type: String,
            default: "#333",
        },
        top: {
            type: String,
            default: "45px",
        },
    },

    data() {
        return {
            show: false,
            links: ["home", "about", "contact", "help"],
        }
    },
    mounted() {
        window.addEventListener("mouseup", (e) => {
            if (!this.$el.contains(e.target)) {
                this.show = false //fires only on click outside
            }
        })
    },

    methods: {
        toggleMenu() {
            this.show = !this.show
        },
    },
}
</script>

